:root{
  --header-height: min(55px, 100vh);
}

header{
  position: fixed;
  transform: translate(0, -100%);
  top: 0px;
  left: 0px;
  right: 0px;
  height: var(--header-height);
  background-color: rgba(0, 0, 0, 0.5);
}

main{
  margin: -8px;
  position: relative;
  top: 0px;
  left: 0px;
  right: 0px;
  height: auto;
}

article{
  position: relative;
  width: 100%;
  min-height: 200px;
}

footer{
  position: fixed;
  transform: translate(0, 0%);
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: calc(var(--header-height) / 2);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

#Impressum{
  width: max-content;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  display: grid;
  place-items: center;
  font-size: 1rem;
  font-weight: bold;
  transition: background-color 300ms;
  user-select: none;
}

#Impressum:hover{
  background-color: rgba(255, 255, 255, 0.25);
}

#Impressum:active{
  background-color: rgba(0, 0, 0, 0.25);
}

dialog{
  position: fixed;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  margin: 0;
  /*border: none;*/
  border-radius: 1rem;
  min-height: 15rem;
  min-width: 30rem;
  overflow: auto;
}

.parent {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  }

.parent > div{
  text-align: center;
  padding: 0.25rem;
  font-size: 1.5rem;
}

.div1{
  padding: 0.5rem;
}

.div6{
  padding: 0.5rem;
}
  
.div1 { grid-area: 1 / 1 / 2 / 5; }
.div2 { grid-area: 2 / 1 / 3 / 2; }
.div3 { grid-area: 2 / 2 / 3 / 5; }
.div4 { grid-area: 3 / 1 / 4 / 2; }
.div5 { grid-area: 3 / 2 / 4 / 5; }
.div6 { grid-area: 4 / 1 / 5 / 5; }
.div7 { grid-area: 5 / 1 / 6 / 2; }
.div8 { grid-area: 5 / 2 / 6 / 5; }
.div9 { grid-area: 6 / 1 / 7 / 2; }
.div10 { grid-area: 6 / 2 / 7 / 5; }

nav{
  position: fixed;
  transform: translate(-100%, 0);
  bottom: 0px;
  left: 0px;
  top: 0px;
  width: min(200px, max(50vw, min(55px, 100vw)));
  background-color: rgba(0, 0, 0, 0.5);
}

header, footer, nav{
  transition: transform 300ms;
}
